

.example__container-block {
  width: 124px;
  margin: 0;
  padding: 14px auto;
  text-align: center;
}

.example__container--flex {
  display: flex;
  justify-content: space-between;
}

.example__container {
  width: 280px;
}

.example__range--flex {
  display: flex;
  min-height: 256px;
}

.example__range {
  width: 140px;
  margin: 0;
}

.example__container-text {
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  font-size: 24px;
  line-height: 24px;
  text-align: center;
}

.example__container-title {
  padding: 0;
  margin: 0;
  color: #c4c4c4;
}

.example__price {
  margin: 34px 0 24px 0;
  text-align: center;
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  text-transform: uppercase;
}

.example__text {
  margin: 0 0 20px 0;
  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
  color: #444444;
}

.example__title {
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  font-size: 36px;
  line-height: 40px;
  margin: 0 0 40px 0;
}

.example__range-left {
  background-image: url("../img/CAT-before@1x.png");
  background-repeat: no-repeat;
  background-position: 0;
  background-size: cover;
}

.example__range-right {
  background-image: url("../img/CAT-after1x.png");
  background-repeat: no-repeat;
  background-position: -140px;
  background-size: cover;
}

@media (min-width: @tablet-width) {
  .example__range--flex {
    position: absolute;
    left: 100px;
    top: 350px;
    min-height: 512px;
  }

  .example__range-left {
    background-image: url("../img/CAT-before@2x.png");
    background-repeat: no-repeat;
    background-position: 0;
    min-width: 280px;
  }

  .example__range-right {
    background-image: url("../img/CAT-after@2x.png");
    background-repeat: no-repeat;
    background-position: -280px;
    min-width: 280px;
  }

  .example__container-padding {
    padding: 43px 0 692px 0;
  }

  .example__contauner--flex-tablet {
    display: flex;
    justify-content: space-between;
  }
}

@media (min-width: @desktop-width) {
  .example__range--flex {
    position: absolute;
    left: 900px;
    top: -50px;
    min-height: 512px;
  }

  .example__container-padding {
    padding: 0 784px 0 0;
  }

  .example__text {
    margin-bottom: 69px;
  }

  .example__contauner--flex-tablet {
    display: inline;
  }
}
